import { Component, OnInit } from '@angular/core';
import { HttpTestService } from 'src/app/http-test.service';
import { Router } from '@angular/router';
import { Location } from '@angular/common';

@Component({
  selector: 'app-support-meal-history',
  templateUrl: './support-meal-history.page.html',
  styleUrls: ['./support-meal-history.page.scss'],
})
export class SupportMealHistoryPage implements OnInit {

  desc: string;
  descrip: boolean;
  descrip2: boolean;
  id_meal: string;
  photo_meal:string;
  
  constructor(private service: HttpTestService, private router: Router, private _location: Location) { 
    
  }

  ionViewWillEnter(){
    this.desc = window.sessionStorage.getItem('description');
    if(this.desc == "null" || this.desc == null){
      this.desc = "";
      
    }
    this.id_meal = window.sessionStorage.getItem('id_meal')
    this.photo_meal=window.sessionStorage.getItem("image_file_name")
  }

  ngOnInit() {
  }

}
